@import url(https://fonts.googleapis.com/css2?family=Allura&family=Poiret+One&family=Raleway&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}



.Main {
  background-image: url(/cocktail-generator/static/media/CocktailBG.028d8251.png);
  background-size: cover;
  min-height: 100vh;
  display: grid;
  grid-template-rows: repeat(4, -webkit-max-content) 1fr;
  grid-template-rows: repeat(4, max-content) 1fr;
  overflow-y: scroll;
}

.heading-container {
  text-align: center;
  font-family: "Allura";

  color: #ff8383;
  width: -webkit-max-content;
  width: max-content;
  background-color: #fff;
  border: 3px solid #c0e1ff;
  border-radius: 2vw;
  margin-top: 5%;
  margin-left: 1.4%;
  -webkit-transform: rotate(-11.5deg);
          transform: rotate(-11.5deg);
}

.heading {
  margin: 2vw;
  font-size: 6.1vw;
}

.sub-heading {
  font-family: "Poiret One";
  color: white;
  text-align: center;
  font-size: 2.2vw;
  text-transform: uppercase;
  margin-top: 2%;
}

.shuffle-btn {
  font-family: "Allura";
  font-size: 2.08vw;
  width: -webkit-max-content;
  width: max-content;
  margin: auto;
  margin-top: 3.6%;
  background-color: #ffce85;
  border-radius: 2.08vw;
  border: none;
  padding: 0.5vw 3.05vw;
  cursor: pointer;
}

.shuffle-btn:hover {
  background-color: #fdc36c;
}

.shuffle-btn:active {
  background-color: #ffce85;
}

.cocktail {
  display: grid;
  grid-template-columns: 0.27fr 0.08fr 0.65fr;
  width: 70%;
  margin: 2.5% auto;
  /* height: 100px; */
}

.cocktail-img {
  width: 100%;
  border-radius: 30px;
  box-shadow: 10px 10px 20px -9px rgba(0, 0, 0, 0.31);
}

.cocktail-info {
  margin-top: 2%;
  color: white;
  display: grid;
  grid-column: 3/4;
  grid-auto-rows: -webkit-max-content;
  grid-auto-rows: max-content;
  -webkit-animation: fadeIn 0.3;
          animation: fadeIn 0.3;
}

.property-and-description {
  display: grid;
  grid-template-columns: 0.5fr 1fr;
  margin-bottom: 4%;
  align-items: top;
}

.property {
  font-family: "Allura";
  font-size: 3.056vw;
}

.description {
  font-family: "Poiret One";
  font-size: 2.5vw;
}

.instructions,
.ingredients {
  font-size: 1.5vw;
  margin-top: 2%;
}

.read-more-btn {
  background: none;
  color: rgb(255, 255, 255);
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  font-family: "Raleway";
  font-size: 1.3vw;
}

.read-more-btn:hover {
  color: rgb(184, 184, 184);
}

